.Customizer{position:absolute;top:0;left:0;width:100%;box-sizing:border-box;padding:50px;display:flex;justify-content:space-between;align-items:flex-start;pointer-events:none}.Customizer .Button{height:50px;cursor:pointer;position:relative;box-sizing:border-box;padding:0 30px;display:flex;justify-content:center;align-items:center;border-radius:10px;background-color:#000;color:#fff;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;text-align:center;border:2px #fff solid;font-size:1rem;font-family:"Roboto","Helvetica","Arial",sans-serif;font-weight:600;line-height:1.5;letter-spacing:.00938em;pointer-events:auto}.Customizer .GlobalActions{display:flex;justify-content:flex-end;flex-grow:1}.Customizer .GlobalActions.black{color:#fff}.Customizer .GlobalActions.white{color:#000}.Customizer .GlobalActions .Button{margin-left:30px}.Customizer .Actions .ItemActions{display:flex;align-items:baseline;margin-bottom:30px}.Customizer .Actions .ItemActions .ColorPicker .picker{width:90px !important;height:90px !important;margin-right:30px;margin-bottom:10px;pointer-events:auto}.Customizer .Actions .ItemActions .ColorPicker input{width:90px !important;height:30px !important;position:relative;box-sizing:border-box;padding:0 10px;border-radius:5px;background-color:#000;color:#fff;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;border:1px #fff solid;font-size:1rem;font-family:"Roboto","Helvetica","Arial",sans-serif;font-weight:500;line-height:1.5;letter-spacing:.00938em;pointer-events:auto}.Customizer .Actions .ItemActions .ColorPicker input:focus,.Customizer .Actions .ItemActions .ColorPicker textarea:focus,.Customizer .Actions .ItemActions .ColorPicker select:focus{outline:none}.Customizer .Actions .ItemActions.black{color:#fff}.Customizer .Actions .ItemActions.white{color:#000}.Customizer .Actions .ItemActions h1{font-size:5em;line-height:.7em;letter-spacing:-3px}.Customizer .Actions .OtherActions.black{color:#fff}.Customizer .Actions .OtherActions.white{color:#000}.Customizer .Actions .OtherActions .MuiFormGroup-row.sliders{flex-shrink:0;pointer-events:auto}.Customizer .Actions .OtherActions .MuiFormGroup-row.sliders label{flex-shrink:0;width:200px;display:flex;flex-direction:column-reverse;align-items:baseline;margin-left:4px;margin-right:30px;margin-bottom:20px}.Customizer .Actions .OtherActions .MuiFormGroup-row.switch{pointer-events:auto}.Customizer .Actions .OtherActions .MuiFormGroup-row.buttons .Button{margin-right:30px;margin-top:30px}.Customizer .Actions .OtherActions .Textures{display:flex;position:relative}.Customizer .Actions .OtherActions .Textures .addWrap{height:50px;cursor:pointer;position:relative;box-sizing:border-box;padding:0 30px;display:flex;justify-content:center;align-items:center;border-radius:10px;background-color:#000;color:#fff;border:2px #fff solid;font-size:1rem;font-family:"Roboto","Helvetica","Arial",sans-serif;font-weight:600;line-height:1.5;letter-spacing:.00938em;pointer-events:auto}.Customizer .Actions .OtherActions .Textures .Texture{background-position:center center;background-size:cover;background-repeat:no-repeat;width:50px;height:50px;background-color:rgba(255,255,255,.452);margin-bottom:20px;margin-right:20px;border-radius:10px;cursor:pointer;position:relative;pointer-events:auto}.Customizer .Actions .OtherActions .Textures .Texture.bin{background-position:top right;background-size:310%}.Customizer .Actions .OtherActions .Textures .Texture.lid{background-position:top left;background-size:66px}.Customizer .Actions .OtherActions .Textures .Texture.active::before{content:"";border:4px #52af77 solid;top:-5px;left:-5px;width:52px;height:52px;border-radius:14px;position:absolute}
